import React, {PureComponent} from "react"
import {Card, Heading, Pane, Tooltip, Text, Button, Link, Paragraph, Icon, IconButton} from "evergreen-ui"
import {Row, Container, Col, Media} from "react-bootstrap"

const Status = Object.freeze({ONGOING: "Ongoing", FINISHED: "Finished", ABANDONED: "Abandoned"})
export class Works extends PureComponent {
	constructor(props) {
		super(props)

		this.state = {
			works: [],
		}

		var obj = {
			name: "Watson",
			tech: ["NodeJs", "MongoDb", "Heroku", "IBM Watson"],
			code: "https://github.com/willroyMyles/Watson-v2/tree/import_watson",
			website: "https://watson-utech-assistant.herokuapp.com/",
			status: Status.FINISHED,
			big: true,
			desc:
				"A chat bot artificial intelligence used to acertain a student's specialization in the field of computer science. this was done in partial fulfillment of my computer science degree. This project utilize IBM watson artificial intelligence agent to identify students responses.",
		}

		var obj1 = {
			name: "Krypto Kitties",
			tech: ["ASP.Net", "C#"],
			code: "https://github.com/willroyMyles/asp.net-e-commerce",
			status: Status.FINISHED,
			big: false,

			website: false,
			desc:
				"This project was completed in fulfillment for an enterprise computing course. a dashboard for the sales of krypto kitties utilizing ASP web forms along with their database tool.",
		}
		var obj2 = {
			name: "Web Services",
			tech: ["ASP.Net", "C#"],
			code: "https://github.com/willroyMyles/asp.net_web_services",
			website: false,
			status: Status.FINISHED,
			big: false,

			desc:
				"This project was completed in fulfillment for enterprise computing course. This project demonstrates accessing and linking of ASP web services, also utilizing their database tools.",
		}

		var obj3 = {
			name: "Mi Casa",
			tech: ["PHP"],
			code: "https://github.com/willroyMyles/php_MiCasa",
			status: Status.FINISHED,
			website: false,
			big: false,

			desc: "A simple brochure website to display real estate properties. This project was completed using php 7",
		}
		var obj4 = {
			name: "Note app",
			tech: ["NodeJs", "MongoDb", "Heroku", "SQL"],
			code: "https://github.com/willroyMyles/noteApp",
			website: "https://takenote1.herokuapp.com/",
			big: false,
			status: Status.ABANDONED,
			desc:
				"A simple note taking app focused on expanding ideas on key words. The motivation for this came from me trying to flesh out ideas with no resource tool to help me. This project was completed with NodeJs and MongoDB.",
		}

		var obj5 = {
			name: "Wrg Autoparts",
			tech: ["NodeJs", "MongoDb", "Heroku"],
			code: "https://github.com/willroyMyles/wrg",
			big: true,
			website: "https://wrg-auto.herokuapp.com/e",
			status: Status.ONGOING,

			desc:
				"This website is a personal project of mine. it serves as a forum where person's searching for a car part will post what they need while the community will fill the request. A different approach to sales forums. This project was completed using NodeJs, MongoDb and hosted on Heroku.",
		}

		this.state.works.push(obj, obj1, obj2, obj3, obj4, obj5)
		this.state.works.sort((a, b) => (a.big == true ? 0 : 1))
	}

	getColor = (status) => {
		if (status == Status.ABANDONED) return "rgba(200,50,50)"
		if (status == Status.ONGOING) return "rgba(50, 200,250)"
		if (status == Status.FINISHED) return "rgba(00, 230, 50)"
	}

	colorStyle = (value) => {
		return {
			// position: "sticky",
			width: 10,
			backgroundColor: this.getColor(value),
			marginTop: "auto",
			marginBottom: "auto",
			margin: 5,
			height: 10,
			borderRadius: 20,
			opacity: 0.5,
		}
	}

	render() {
		return (
			<Pane background="tint1" elevation={2} style={{borderRadius: 4, marginTop: 60, padding: 20, overflow: "hidden"}}>
				<Heading size={900}>Notable Works</Heading>
				<div class="space" />
				<Container>
					<Row style={{padding: 15, backgroundColor: "white", margin: 3, borderRadius: 7, opacity: 1}}>
						<Col style={{justifyContent: "center", display: "flex", verticalAlign: "middle"}}>
							<div style={this.colorStyle(Status.FINISHED)} />
							{Status.FINISHED.toString()}
						</Col>
						<Col style={{justifyContent: "center", display: "flex", verticalAlign: "middle"}}>
							<div style={this.colorStyle(Status.ONGOING)} /> {Status.ONGOING.toString()}
						</Col>
						<Col style={{justifyContent: "center", display: "flex", verticalAlign: "middle"}}>
							<div style={this.colorStyle(Status.ABANDONED)} /> {Status.ABANDONED.toString()}
						</Col>
					</Row>
					<Row>
						{this.state.works.map((value, index) => {
							return (
								<Col md={value.big ? "12" : "6"}>
									<Container
										as={Pane}
										elevation="1"
										hoverElevation="2"
										style={{
											marginTop: 20,
											marginBottom: 10,
											backgroundColor: "white",
											padding: 10,
											borderRadius: 7,
										}}>
										<div style={this.colorStyle(value.status)} />
										<Row>
											<Col md="3" style={{alignItems: "left", textAlign: "left", padding: 10, paddingLeft: 30}}>
												<span style={{fontSize: 25, fontWeight: "bold", color: "rgba(0,0,0,.65)"}}>{value.name}</span>
											</Col>
											<Col md="7">
												<Paragraph>{value.desc}</Paragraph>
											</Col>
											<Col md="1">
												<Tooltip content={`visit code at ${value.website}`}>
													<Link color="green" href={value.code}>
														code
													</Link>
												</Tooltip>
											</Col>
											{value.website && (
												<Col md="1">
													<Tooltip content={`visit website at ${value.website}`}>
														<Link color="green" href={value.website}>
															website
														</Link>
													</Tooltip>
												</Col>
											)}
										</Row>
										<Row style={{marginTop: 5, padding: 10, backgroundColor: "rgba(10,10,15,0)"}}>
											{value.tech.map((tech, idx) => {
												return (
													<Col md="2" style={{borderWidth: 2}}>
														<span
															style={{
																fontSize: 14,
																backgroundColor: "rgba(100,200,100, .1)",
																color: "rgba(100,100,100)",
																borderRadius: 30,
																padding: 5,
																paddingLeft: 15,
																paddingRight: 15,
																marginTop: 15,
															}}>
															{tech}
														</span>
													</Col>
												)
											})}
											{/* <IconButton icon="media" size={30} /> */}
										</Row>
									</Container>
								</Col>
							)
						})}
					</Row>
				</Container>
			</Pane>
		)
	}
}

const styles = {
	container: {
		textAlign: "left",
	},
}

export default Works
